import React from 'react';
import { NavLink } from 'react-router-dom';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
    faInstagram,
    faBehance
} from "@fortawesome/free-brands-svg-icons";
import style from './nav.module.css';


const Nav = React.memo(({buttonName, onSubmit, myName, works, about, contacts}) => {

    return (
        <div className={style.wrapper}>
            <div>
                <p>{myName}</p>
            </div>
            <NavLink to='/main'>{works}</NavLink>
            <NavLink to='/about'>{about}</NavLink>
            <NavLink to='/contacts'>{contacts}</NavLink>
            <div className = {style.icon}>
                <a href="https://www.instagram.com/crocus_focus/" target = "_blank" rel='noreferrer' ><FontAwesomeIcon icon={faInstagram} size="1x" /></a>
                <a href="https://www.behance.net/jennie_kuznetsova" target = "_blank" rel='noreferrer' ><FontAwesomeIcon icon={faBehance} size="1x" /></a>
            </div>
            <form onSubmit = {onSubmit}>
              <button>({buttonName})</button>
            </form>
        </div>
    );
});

export default Nav;